<template>
  <div>
    <h1>
      {{ this.$store.state.Content[this.$store.state.activeContent].pageTitle }}
    </h1>

    <div class="content-wrapper">
      <transition-group name="fade" mode="in-out" appear>
        <aside
          v-for="(mainTopic, title) in this.$store.state.Content[
            this.$store.state.activeContent
          ].sections"
          :key="mainTopic"
        >
          <h2>{{ title }}</h2>
          <l-button
            v-for="(subTopic, subTitle) in mainTopic"
            :key="subTitle"
            @click="shownCard = [subTitle, subTopic]"
            >{{ subTitle }}</l-button
          >
        </aside>
      </transition-group>

      <teleport to=".card-plate-handler">
        <z-plate>
          <card>
            <template v-slot:h3>{{ shownCard[0] }}</template>
            <template v-slot:default
              ><div v-html="shownCard[1]"></div
            ></template>
          </card>
        </z-plate>
      </teleport>
    </div>
  </div>
</template>

<script>
import card from "./Card.vue";
import ZPlate from "./ZPlate.vue";
import LButton from "./LButton.vue";
export default {
  name: "ZContent",
  components: { card, ZPlate, LButton },
  data() {
    return {
      shownCard: [],
      activeContent: this.$store.state.activeContent,
    };
  },
};
</script>
