<template>
  <button @click="goBack($event)"><i class="fas fa-arrow-left"></i></button>
</template>

<script>
export default {
  name: "back",
  methods: {
    goBack() {
      document
        .querySelector(".card-plate-handler")
        .classList.remove("plate-show");
    },
  },
};
</script>

